@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap);
html{
    font-family: 'Space Grotesk', sans-serif;
    background: url(/zbys1av/pass-js/static/media/img-1.8b0b3937.png);
    background-size: cover;
}

::selection {
    background: #ffa600;
    color: white;
}
input{
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-color: rgba(255, 255, 255, 0.2);
    border: 1px solid white;
    height: 20px;
    width: 170px;
    /* margin: 5px; */
    transition: all 0.5s;
    color: white;
}

input::-webkit-input-placeholder{
    color: rgb(68, 68, 68);
}

input:-ms-input-placeholder{
    color: rgb(68, 68, 68);
}

input::placeholder{
    color: rgb(68, 68, 68);
}

input:focus::-webkit-input-placeholder{
    color: rgb(255, 255, 255);
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

input:focus:-ms-input-placeholder{
    color: rgb(255, 255, 255);
    -ms-transition: all 0.5s;
    transition: all 0.5s;
}

input:focus::placeholder{
    color: rgb(255, 255, 255);
    transition: all 0.5s;
}

input:hover{
    background-color: rgba(255, 255, 255, 0.4);
    color: white;
    transition: all 0.5s;
}

input:focus{
    background-color: rgba(255, 255, 255, 0.6);
    outline: transparent;
    transition: all 0.5s;
}

button{
    display: block;
    margin: auto;
    width: 170px;
    border: 1px solid #000;
    background-color: rgba(110, 110, 110, 0.4);
    color: white;
    transition: all 0.5s;
}

button:focus{
    outline: transparent;
}

button:hover{
    background-color: rgba(255, 255, 255, 0.4);
    border: 1px solid #fff;
    transition: all 0.5s;
}

.passResult{
    display: flex;
    justify-content: center;
    font-size: 25px;
    transition: all 0.5s;
}

.passResult:hover{
    color: #fff;
    transition: all 0.5s;
}
h1{
    font-size: 25px;
}
p{
    font-size: 10px;
    color: rgba(255, 255, 255, 0.3);
    position: fixed; 
    bottom: 0; 
    width:100%; 
    text-align: center;
}
